<template>
    <div id="swipeupload">

        <img src="../assets/phone.jpg" alt="" id="bg">
        
        <div id="inner">
            <p>请点击上传图片</p>
                <input type="file" name='f1' accept="image/*" id='imgfile' @change="show">
            <p>图片预览：</p>
            <img id="imgpreview" src='' style="display:none">

            <p v-show="flag">请上传活动链接</p>
            <el-input type="text" style="width:100%" v-model='eventlink' id="eventlink" v-show="flag" placeholder="请输入活动URL"></el-input>
            <el-button type="primary" @click="upload" v-show="flag" style="margin-top:10px">上传</el-button>
            <el-button type="primary" @click="bgupload"  v-show="!flag" style="margin-top:10px">背景上传</el-button>
        </div>
        
        
      
      
    </div>
</template>

<script>
export default {
    data(){
        return {img:'',
            filename:'',
            flag:true,
            eventlink:''
        }
    },
    methods:{
        bgupload(){
            var file = document.getElementById('imgfile');
            var eventlink = document.getElementById('eventlink');
            let img = file.files[0];
            let formData = new FormData();
            formData.append(this.filename, img);
            if(typeof(formData.get(this.filename)) === 'object'){
                this.$axios({
                        method:'post',
                        url:'http://101.71.247.41:2138/upload',
                        data:formData,
                        headers:{
                            'Content-Type':'multipart/form-data'
                        }
                    }).then(res=>{
                        alert(res.data);
                    }).catch(err=>{console.log(err)});
            }else{
                alert('图片为空！');
            }
        },
        upload(){
            var file = document.getElementById('imgfile');
            var eventlink = document.getElementById('eventlink');
            let img = file.files[0];
            let formData = new FormData();
            formData.append(this.filename, img);
            formData.append('url',eventlink.value)
            if(typeof(formData.get(this.filename)) === 'object'){
                var exg1 = /^http:\/\//g
                var exg2 = /^https:\/\//g
                if(formData.get('url').match(exg1) || formData.get('url').match(exg2)){
                    // alert('输入正确！');
                    this.$axios({
                        method:'post',
                        url:'http://101.71.247.41:2138/upload',
                        data:formData,
                        headers:{
                            'Content-Type':'multipart/form-data'
                        }
                    }).then(res=>{
                        alert(res.data);
                    }).catch(err=>{console.log(err)});
                }else{
                    alert('请输入正确的URL地址！')
                }

            }else{
                alert('图片或链接为空！')
            }
            
            
        },
        show(){
            var file = document.getElementById('imgfile');
            var imgpreview = document.getElementById('imgpreview');
            var img = file.files[0];
            var reader = new FileReader();
            var that = this;
            reader.onload = function(e){
                var data = e.target.result;
                var image = new Image;
                image.onload = ()=>{
                    var sw = /^swipe/;
                    var lb = /^longbanner/;
                    if(that.filename.match(sw) ||  that.filename.match(lb)){
                        if(image.width == 570 && image.height == 140){
                            imgpreview.src = data;
                            imgpreview.style.display='block'
                        }else{
                            alert('图片格式错误！正确格式:570*140')
                            file.value = '';
                        }
                    }else if(that.filename == 'bg'){
                        if(image.width == 640 && image.height >1384){
                            imgpreview.src = data;
                            imgpreview.style.display='block'
                        }else{
                            alert('图片格式错误！宽度必须为640，长度需大于1385')
                            file.value = '';
                        }
                    }else{
                        if(image.width == 268 && image.height == 150){
                            imgpreview.src = data;
                            imgpreview.style.display='block'
                        }else{
                            alert('图片格式错误！正确格式:268*150')
                            file.value = '';
                        }
                    }


                    
                }
                image.src = data;
                
            }
            reader.readAsDataURL(img);
            
        }
    },
    mounted(){
    if(!sessionStorage.user){
      this.$router.push('/login')
    }else{
        this.filename = this.$route.params.id;
        if(this.$route.params.id == 'bg'){
            this.flag = false;
        }
    }
  }
}
</script>

<style scoped>
    #swipeupload{
        width: 375px;
        height:812px;
        margin: auto;
        
    }
    
    #imgpreview{
        width: 334px;
        height: 82px;
    }
    #bg{
    position: absolute;
    top: 0;
    z-index: -1;
    }
    #inner{
    margin-top: 45px;
    margin-left: 35px;
    
    }
    
</style>


